import React, { Component } from 'react';
import { Router, Route, IndexRoute, hashHistory } from 'react-router'
import Header from './Header.js'
// import Home from './components/dragDrop/Home.js'
// import TRex, {Simplest, Medium, Toughest} from './components/tRexGame/App.js'
// import Brainwaves from './components/brainwaves/App.js'
// import './App.css';
import Loadable from 'react-loadable';

const LoadableBrainwaveComponent = Loadable({
  loader: () => import('./components/brainwaves/App.js'),
  loading() {
    return <div>Loading...</div>
  }
})

const LoadableHomeComponent = Loadable({
  loader: () => import('./components/dragDrop/Home.js'),
  loading() {
    return <div>Loading...</div>
  }
})

const Trex = Loadable({
  loader: () => import('./components/tRexGame/App.js'),
  loading() {
    return <div>Loading...</div>
  }
})

const Simplest = Loadable({
  loader: () => import('./components/tRexGame/App.js'),
  render(loaded, props) {
    let Component = loaded.Simplest;
    return <Component {...props}/>;
  }
})

const Medium = Loadable({
  loader: () => import('./components/tRexGame/App.js'),
  render(loaded, props) {
    let Component = loaded.Medium;
    return <Component {...props}/>;
  }
})

const Toughest = Loadable({
  loader: () => import('./components/tRexGame/App.js'),
  render(loaded, props) {
    let Component = loaded.Toughest;
    return <Component {...props}/>;
  }
})

class App extends Component {
  render() {
    return (
      <div className="App">
        <Router history={hashHistory}>
          <Route path='/' component={Container}>
            <IndexRoute component={LoadableHomeComponent} />
            <Route path='trex' component={Trex}>
              <IndexRoute component={Trex} />
              <Route path='simple' component={Simplest} />
              <Route path='medium' component={Medium} /> 
              <Route path='tough' component={Toughest} /> 
            </Route>
            <Route path='brainwaves' component={LoadableBrainwaveComponent} /> 
            <Route path='*' component={NotFound} />
          </Route>
        </Router>
      </div>
    );
  }
}

const NotFound = () => (
  <h1>404.. This page is not found!</h1>)
const Container = (props) => <div>
  <Header />
  {props.children}
</div>

export default App;



